.toolbar {
  width: 100%;
  background-image: linear-gradient(to top, rgb(0 0 0 / 0%), rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.6));
  display: flex;
  flex-direction: column;
  align-items: center;
  padding: 2vmin 0 1vmin 0;
}

.content-toolbar {
  width: 100vmin;
  max-width: 500px;
  height: 7vmin;
  padding-right: 1.5vmin;
  background-image: url(/sevengalaxies-app/static/media/Logo_wide.35138d32.png);
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center;
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: center;
}

.menu {
  height: 100%;
  width: auto;
  display: flex;
  flex-direction: column;
  align-items: center;
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 9/16) {
  .toolbar {
    /* padding: 2% 0 1% 0; */
    padding: 1.12vmax 0 0.56vmax 0;
  }
  .content-toolbar {
    /*  
    width: 100vmin;
    height: 7vmin;
    padding-right: 1.5vmin; */
    width: 55.75vmax;
    height: 3.9vmax;
    padding-right: 0.84vmax;
  }
}

@media screen and (min-width: 300px) And (max-height: 890px) And (min-aspect-ratio: 1/1) {
  .toolbar {
    /* padding: 2% 0 1% 0; */
    padding: 1.12vmin 0 0.56vmin 0;
  }
  .content-toolbar {
    /*  
    width: 100vmin;
    height: 7vmin;
    padding-right: 1.5vmin; */
    width: 55.75vmin;
    height: 3.9vmin;
    padding-right: 0.84vmin;
  }
}

@media screen and (min-width: 500px) And (min-height: 891px) {
  .toolbar {
    padding: 10.0156px 0 5px 0;
  }
  .content-toolbar {
    width: 500px;
    height: 35px;
    padding-right: 7.5px;
  }
}
