}

a {
  color: #fff;
  cursor: pointer;
  text-decoration: unset;
}
.modal {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  background-color: rgba(0, 0, 0, 0.8);
  display: flex;
  justify-content: center;
  align-items: center;
  /* transition: all 1s linear; */
  -webkit-animation: fadein 0.1s linear;
          animation: fadein 0.1s linear;
  color: #fff;
}

.container {
  background-color: rgba(0, 0, 0, .85);
  border: solid 0.2px #4a4a4a;
  width: 80vmin;
  height: 40vmin;
  max-width: 400px;
  max-height: 200px;
  /* background-color: #36393f; */
  box-shadow: 0 0 0 1px rgba(32, 34, 37, .6), 0 2px 10px 0 rgba(0, 0, 0, .2);
  border-radius: 2px;
  margin: 0 auto;
  pointer-events: all;
  position: relative;
  display: grid;
  grid-template-rows: 2fr 1fr;
  justify-content: center;
  align-items: center;
  /* transition: all 1s linear; */
  -webkit-animation: fadein 0.1s linear;
          animation: fadein 0.1s linear;
}

.message {
  padding: calc(10px + 2vmin);
  text-align: start;
  /* margin-bottom: 10px; */
  font-size: calc(9px + 1vmin);
}

.options {
  width: 75vmin;
  max-width: 375px;
  margin: 0 calc(5px + 1vmin) 0 calc(5px + 1vmin);
  padding: calc(5px + 1vmin) 0;
  border-radius: 0 0 5px 5px;
  border-top: 2px solid #414141;
  display: flex;
  justify-content: flex-end;
  grid-column-gap: 20px;
  -webkit-column-gap: 20px;
          column-gap: 20px;
}

.options .action {
  -webkit-appearance: button;
     -moz-appearance: button;
          appearance: button;
  width: calc(60px + 2vmin);
  min-height: 30px;
  min-width: 90px;
  font-size: calc(8px + 1vmin);
  font-weight: bold;
  display: inline-block;
  text-align: center;
  align-items: flex-start;
  cursor: pointer;
  color: #fff;
}

.options .cancel {
  font-size: calc(8px + 1vmin);
  font-weight: bold;
  color: #fff;
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
}
