  padding-top: 6vmin;
  width: 100%;
  height: 25vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.theme-simbolo img {
  width: 40vmin;
}

.theme-title {
  width: 100%;
  padding: 0 0 0 1vmin;
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* .theme-escolha {
  width: 100%;
} */

/* anima ao ativar */

.compra-ativa {
  will-change: animate;
  /* animation: dropshadow 1.5s ease-in; */
  -webkit-animation: color-on-off 1s ease-in;
          animation: color-on-off 1s ease-in;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
}

/* anima cada modificação de elemento*/

.teste {
  transition: background-image 2s linear;
}

.entrada {
  -webkit-animation: fadein 0.5s linear;
          animation: fadein 0.5s linear;
}

.destaque {
  -webkit-animation: dropshadow 0.3s ease-in-out;
          animation: dropshadow 0.3s ease-in-out;
}

.destaque-bt {
  -webkit-animation: dropshadow 0.08s linear;
          animation: dropshadow 0.08s linear;
}

.destaque-on {
  -webkit-animation: color-on 0.4s linear;
          animation: color-on 0.4s linear;
}

@-webkit-keyframes zoomin7G {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes zoomin7G {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes zoomin {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2) translate3d(3vmin, 3vmin, 0px);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes zoomin {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2) translate3d(3vmin, 3vmin, 0px);
  }
  100% {
    transform: scale(1);
  }
}

@-webkit-keyframes breath {
  0% {
    /* background-position: right; */
