.app {
  height: 100%;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}

.corporation {
  padding-bottom: 2vmin;
}

/* .app>.toolbar {
  flex: 0 0 auto;
} */

/* .app>.header+main {
  flex: 1 1 auto;
  display: flex;
  flex-direction: row;
} */

main {
  display: flex;
  flex-direction: row;
  flex-grow: 1;
}

.background-control {
  width: 8vmin;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.game {
  max-width: 960px;
  margin: 0 auto;
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  align-items: center;
  row-gap: 1vmin;
  text-align: center;
  color: white;
  user-select: none;
  flex: 2 0 auto;
}

.corporation {
  display: flex;
  flex-direction: column;
  align-items: center;
  text-shadow: 2px 3px 8px rgba(0, 0, 0, .8);
  font-size: 2.4vmin;
  font-weight: bold;
  color: white;
}

.rodada {
  order: 1;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  width: 80vmin;
  padding: 0 0 0 1vmin;
  animation: fadein 0.1s linear;
}

.rodada-nova {
  width: 35vmin;
  padding: 2.3vmin 0 1.5vmin 0;
  border-top: 35vmin;
  display: flex;
  flex-direction: column;
  flex-grow: 1;
}

.rodada-escolha {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  transition: all 0.25s linear;
  flex-grow: 1;
}

.rodada-button, .rodada-button-new {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  flex-grow: 1;
  cursor: pointer;
}

.bt-first {
  margin-right: 3vmin;
}

.rodada-button p {
  font-size: 11vmin;
  text-shadow: none;
}

.rodada-button-new p {
  font-family: "Agency FB", sans-serif;
  font-size: 9vmin;
}

.rodada-button-new img {
  width: 40vmin;
  pointer-events: none;
}

.rodada-num {
  background-image: url(./images/Rodada.png);
  background-repeat: no-repeat;
  background-position: center;
  /* background-repeat: round; */
  background-size: contain;
  display: flex;
  justify-content: center;
  align-items: center;
  width: 26.3vmin;
  font-size: 14vmin;
  padding: 3.7vmin 0 4vmin 0;
  margin-left: 3vmin;
}

.nivel {
  order: 2;
  background-image: url(./images/Nivel.png);
  animation: fadein 0.2s linear;
}

.energia {
  order: 4;
  background-image: url(./images/Energia.png);
  animation: fadein 0.3s linear;
}

.fortaleza {
  order: 5;
  background-image: url(./images/Fortaleza.png);
  animation: fadein 0.4s linear;
}

.nivel, .energia, .fortaleza {
  background-repeat: no-repeat;
  background-position: center;
  /* background-repeat: round; */
  background-size: contain;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: 80vmin;
  padding-top: 3vmin;
  /* max-height: 224px;/*/
  /*remover*/
  /*height: 224px; /*/
  /* border-style: solid;
  border-width: calc(0.3vmin);
  border-color: #c7c3cf; */
}

.fortaleza {
  padding-top: 3.2vmin;
  padding-bottom: 1vmin;
  justify-content: space-around;
}

.reserva-movel {
  /* display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between; */
  display: grid;
  justify-content: center;
  align-items: center;
  grid-template-columns: 1fr 2fr 1fr;
  width: 80%;
  column-gap: 20px;
  font-size: 26vmin;
  padding-bottom: .5%;
}

.reserva-movel#fortaleza {
  height: 26vmin;
}

.bt-movel {
  display: flex;
  /* background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset; */
  padding-top: 20%;
  cursor: pointer;
}

.bt-movel img {
  pointer-events: none;
  width: 13vmin;
  height: 13vmin;
}

/* .bt-movel:hover {
  animation: dropshadow 2s ease-in-out;
  ;
  /* animation: spin 3s linear infinite; 
} */

.reserva-conquista {
  display: grid;
  justify-content: center;
  grid-template-columns: 1fr 1fr 1fr;
  width: 51%;
  font-size: 11vmin;
}

.reserva-conquista button {
  display: flex;
  align-items: center;
  justify-content: center;
}

.reserva-conquista button img {
  pointer-events: none;
}

.reserva-conquista p {
  background-color: rgba(0, 0, 0, .55);
}

.reserva-conquista img {
  width: 13vmin;
}

.compra {
  width: 80vmin;
  padding: 2vmin 0 3vmin 0;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.compra-aviso {
  width: 100%;
  height: 5.4vmin;
  padding: 0 5vmin 0 5vmin;
  font-size: 4vmin;
  text-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
  font-weight: bold;
  font-style: italic;
  display: grid;
  grid-template-columns: 1fr 2fr 1fr;
  justify-items: center;
  align-items: center;
  animation: fadein 0.5s linear;
}

.compra-divisa {
  width: 100%;
  height: 5.4vmin;
  padding: 0 5vmin 0 5vmin;
  font-size: 4vmin;
  display: flex;
  flex-direction: row;
  align-items: center;
}

.compra-linha {
  width: 100%;
  /* border-top: 2px solid #fff;/ */
  height: 0.35vmin;
  background-color: #fff;
  -webkit-box-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
  -moz-box-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
  box-shadow: 1px 2px 6px rgba(0, 0, 0, .3);
}

.compra-card {
  width: 70%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  font-size: 8vmin;
}

.compra-card button {
  background-color: unset;
  box-shadow: unset;
  border: unset;
  background-image: unset;
}

.compra-marco {
  display: flex;
  flex-direction: row;
}

.marco-checked {
  display: flex;
  flex-direction: column;
  width: 6vmin;
}

.checked {
  height: 65%;
  width: auto;
  /*display: flex;
  flex-direction: column;
  align-items: top; */
}

.marco-checked img {
  width: 6vmin;
}

.theme {
  width: 100%;
  height: 100vh;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 10;
  color: #fff;
  background-color: rgba(0, 0, 0);
  display: flex;
  justify-content: center;
  align-items: center;
  user-select: none;
}

.theme p {
  /* font-family: 'Open Sans', sans-serif; -*/
  font-family: "Teko", sans-serif;
}

.theme-container {
  height: 100vh;
  width: 100vw;
  display: flex;
  flex-direction: column;
  align-items: center;
  flex-grow: 1;
  font-size: 5.5vmin;
}

.theme-block {
  width: 100vw;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  overflow: hidden;
  flex-grow: 1;
  /* overflow: hidden; */
}

.theme-background {
  width: 100%;
  height: 100%;
  /* position: absolute; */
  z-index: -1;
  background-position: top;
  background-repeat: no-repeat;
  background-size: cover;
  /* transition: transform 2s; */
  animation: zoomin 50s linear 700ms infinite;
}

.bg-sevenG {
  background-image: url(./pages/SelectTheme/images/Tema_SevenG.jpg);
  animation: zoomin7G 40s linear 700ms infinite;
}

.bg-stroj {
  background-image: url(./pages/SelectTheme/images/Tema_Stroj.jpg);
}

.bg-gaia {
  background-image: url(./pages/SelectTheme/images/Tema_Gaia.jpg);
}

.theme-itens {
  position: absolute;
  width: 100vw;
  height: 33.34vh;
}

.theme-itens-flex {
  display: flex;
  height: 100%;
  flex-direction: column;
  align-items: center;
}

.theme-top {
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
  align-items: center;
  width: 60vmin;
  font-size: 7vmin;
  border-top: 10vmin solid rgba(0, 0, 0, .9);
  border-left: 5vmin solid transparent;
  border-right: 5vmin solid transparent;
  height: 0;
}

.theme-simbolo, #sevenG {
  padding-top: 4vmin;
}

.theme-simbolo {
  padding-top: 6vmin;
  width: 100%;
  height: 25vh;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  flex-grow: 1;
}

.theme-simbolo img {
  width: 40vmin;
}

.theme-title {
  width: 100%;
  padding: 0 0 0 1vmin;
  display: flex;
  flex-direction: row;
  align-items: center;
}

/* .theme-escolha {
  width: 100%;
} */

/* anima ao ativar */

.compra-ativa {
  will-change: animate;
  /* animation: dropshadow 1.5s ease-in; */
  animation: color-on-off 1s ease-in;
  animation-iteration-count: infinite;
}

/* anima cada modificação de elemento*/

.teste {
  transition: background-image 2s linear;
}

.entrada {
  animation: fadein 0.5s linear;
}

.destaque {
  animation: dropshadow 0.3s ease-in-out;
}

.destaque-bt {
  animation: dropshadow 0.08s linear;
}

.destaque-on {
  animation: color-on 0.4s linear;
}

@keyframes zoomin7G {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes zoomin {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.2) translate3d(3vmin, 3vmin, 0px);
  }
  100% {
    transform: scale(1);
  }
}

@keyframes breath {
  0% {
    /* background-position: right; */
    background-size: cover;
  }
  50% {
    /* background-position: top; */
    background-size: 170vmax;
  }
  100% {
    /* background-position: right; */
    background-size: 100vmax;
  }
}

/** dimensões máximas esperadas **/

@media screen and (min-width: 500px) And (min-height: 891px) {
  /* .background-control {
    width: 40px;
  } */
  .game {
    row-gap: 4.62px;
  }
  .corporation {
    font-size: 12px;
    padding-bottom: 10px;
  }
  .rodada {
    width: 400px;
    padding: 0 0 0 6px;
  }
  .rodada-nova {
    width: 248px;
    padding: 11.5px 0 7.5px 0;
  }
  .rodada-num {
    width: 131px;
    height: 114px;
    font-size: 70px;
    padding: 0 0 0 0;
    margin-left: 15px;
  }
  .rodada-button p {
    font-size: 55px;
  }
  .bt-first {
    margin-right: 15px;
  }
  .rodada-button-new p {
    font-size: 45px;
  }
  .rodada-button-new img {
    width: 200px;
  }
  .nivel, .energia, .fortaleza {
    width: 400px;
    height: 224px;
    padding-top: 15px;
  }
  .fortaleza {
    height: 242px;
    padding-top: 16px;
    padding-bottom: 5px;
  }
  .reserva-movel {
    padding-bottom: 0;
    font-size: 130px;
  }
  .reserva-movel#fortaleza {
    height: 130px;
  }
  .reserva-conquista {
    font-size: 55px;
  }
  .reserva-conquista img {
    width: 65px;
  }
  .bt-movel img {
    width: 65px;
    height: 65px;
  }
  .compra {
    width: 400px;
    padding: 8px 0 12px 0;
  }
  .compra-aviso {
    height: 27px;
    padding: 0 25px 0 25px;
    font-size: 20px;
  }
  .compra-divisa {
    height: 27px;
    padding: 0 25px 0 25px;
    font-size: 20px;
  }
  .compra-linha {
    height: 1.75px;
  }
  .compra-card {
    font-size: 40px;
  }
  .marco-checked {
    width: 29.97px;
  }
  .marco-checked img {
    width: 29.97px;
  }
  .theme-container {
    font-size: 27.5px;
  }
  .theme-top {
    width: 300px;
    font-size: 35px;
    border-top: 50px solid rgba(0, 0, 0, .7);
    border-left: 25px solid transparent;
    border-right: 25px solid transparent;
  }
  .theme-simbolo, #sevenG {
    padding-top: 20px;
  }
  .theme-simbolo {
    padding-top: 30px;
  }
  .theme-simbolo img {
    width: 200px;
  }
  .theme-title {
    padding: 0 0 0 5px;
  }
}